<template>
  <div class="chatgroup">
    <div class="navHead">
      <a-icon type="left" class="leftIcon" @click="backRouter" />
      <span>{{title}}</span>
      <a-icon type="team" class="userIcon" @click="todetail" />
    </div>

    <div class="contentBox" ref="msgWrapper">
      <div class="boxWrapper">
        <div
          class="userChat"
          v-for="(message,index) in groupMsgList"
          :key="index"
          :class="{self:message.from == $store.state.user.username}"
        >
          <a-avatar icon="user" class="avatar" />
          <span class="userid">{{message.from}} :</span>
          <div class="bubble">
            <p>{{message.data}}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="sendControl">
      <input type="text" v-model="msg" />
      <a-icon type="smile" class="smileIcon" />
      <button :class="['btn', {active:btnActive}]" @click="send">发送</button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { mapGetters, mapActions, mapState } from "vuex";
import BScroll from "better-scroll";
export default {
  name: "chatgroup",
  data() {
    return {
      msg: "",
    };
  },
  computed: {
    ...mapState(["title"]),
    ...mapGetters(["groupMsgList"]),
    btnActive() {
      if (this.msg.trim()) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    ...mapActions(["sendGroupText", "listGroupMember"]),
    backRouter() {
      this.$router.go(-1);
    },
    send() {
      // console.log(this.$route.params.username,this.msg);
      if (!this.msg.trim()) {
        return;
      }
      let opt = {
        gid: this.$route.params.groupid,
        content: this.msg
      };
      // console.log(opt);
      this.sendGroupText(opt);
      this.msg = "";
    },
    initalBScroller() {
      if (!this.msgScroller) {
        this.msgScroller = new BScroll(this.$refs.msgWrapper, {
          click: true
        });
      } else {
        this.msgScroller.refresh();
      }
    },
    todetail() {
      this.$router.push({ name: "groupdetail" });
      // console.log(this.$route.params.groupid)
      this.listGroupMember(this.$route.params.groupid);
    }
  },
  watch: {
    groupMsgList() {
      this.initalBScroller();
      this.msgScroller.scrollTo(0, this.msgScroller.maxScrollY, 500);
      // console.log(this.msgScroller.maxScrollY, "watch");
    }
  },
  beforeMount() {
    //读取本地存储
    // console.log(this.$store.state.user.username);
    let chatMsg = localStorage.getItem(this.$store.state.user.username);
    if (chatMsg) {
      this.$store.state.msgDataSource = JSON.parse(chatMsg);
    }
  },
  mounted() {
    this.initalBScroller();
    // console.log(this.msgScroller.maxScrollY, "mouted");
    this.msgScroller.scrollTo(0, this.msgScroller.maxScrollY);
  },
  updated() {
    this.initalBScroller();
    this.msgScroller.scrollTo(0, this.msgScroller.maxScrollY);
  },
  beforeDestroy() {
    this.msgScroller.destroy();
    this.msgScroller = null;
  }
};
</script>

